import React, { useState } from "react";
import Header from "./commonComponent/header";
import SideBar from "./commonComponent/sideBar";
import { Link, useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { useAlert } from "react-alert";
import { addBanner } from "../apiServices/home/homeHttpService";
import { showAlert } from "./commonComponent/alertManager";

function AddPromo() {
  const [file, setFile] = useState(null);
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    mode: "onChange",
  });

  const alert = useAlert();
  const navigate = useNavigate();

  const handleImageUpload = (event) => {
    const selectedFile = event.target.files[0];
    setFile(selectedFile);
  };

  const onSubmit = async (data) => {
    if (!file?.name) {
      alert.show("Please upload a file");
      return;
    }
    const formData = new FormData();

    formData.append("image", file);

    Object.keys(data).forEach((key) => {
      formData.append(key, data[key]);
    });
    const response = await addBanner(formData);
    if (!response.error) {
      navigate("/admin/promotional-banner");
      showAlert(alert, response.message, { timeout: 3000 });
      reset();
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };
  return (
    <>
      <Header />
      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <Link to="/admin/promotional-banner" className="comman_btn w-fit">
            <i className="fa fa-arrow-left" />
          </Link>

          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row comman_table pt-md-4 pb-md-4 pb-3 px-md-4 px-2">
                <h3 className="mb-3 fs-5 fw-semibold">
                  Add Promotional Banner
                </h3>
                <form className="form-design" onSubmit={handleSubmit(onSubmit)}>
                  <div className="row">
                    <div className="mb-3">
                      {file?.name ? (
                        <div className="img-wrap">
                          <img
                            src={file ? URL.createObjectURL(file) : ""}
                            alt="Uploaded Image"
                          />
                        </div>
                      ) : (
                        <label
                          htmlFor="photo-upload"
                          className="custom-file-upload"
                        >
                          <div className="text-center">
                            <i className="fas fa-cloud-upload-alt" />
                            <br />
                            Upload Image
                          </div>
                        </label>
                      )}
                      <input
                        id="photo-upload"
                        type="file"
                        onChange={(e) => handleImageUpload(e)}
                      />
                    </div>
                    <div className="col-md-6">
                      <div className="form-group">
                        <label>Banner Name</label>
                        <input
                          type="text"
                          className="form-control"
                          name="bannerName"
                          id="bannerName"
                          {...register("bannerName", {
                            required: true,
                          })}
                        />
                        {errors.bannerName &&
                          errors.bannerName.type === "required" && (
                            <p className="form-error">This field is required</p>
                          )}
                      </div>
                    </div>

                    <div className="col-md-6">
                      <div className="form-group">
                        <label>Banner Description</label>
                        <textarea
                          type="text"
                          className="form-control"
                          name="description"
                          id="description"
                          {...register("description", {
                            required: true,
                          })}
                        />
                        {errors.description &&
                          errors.description.type === "required" && (
                            <p className="form-error">This field is required</p>
                          )}
                      </div>
                    </div>
                    <div className="col-12">
                      <button type="submit" className="comman_btn w-fit">
                        Add Banner
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default AddPromo;
